/*eslint-disable*/module.exports={messages:{"- Ein Anspruch auf **Aufwendungsersatz** ist das Recht auf **Erstattung der Kosten**, die bei der **Beseitigung des Mangels** entstanden sind.\n- Sie dürfen den Mangel selbst beseitigen, wenn die Vermieterin **mit der Beseitigung des Mangels in Verzug** ist, oder es sich um eine **Not- oder Eilmaßnahme** handelt.\n- Meistens sollten Sie den Mangel erst selbst beseitigen, wenn Sie Ihre Vermieterin mit einer Frist zur Beseitigung aufgefordert haben, und der Mangel danach nicht behoben wurde.":"- A claim to **reimbursement of expense** is the right to **reimbursement of the costs** incurred in the **elimination of the defect**.\n- You may rectify the defect yourself if the lessor is in arrears with the elimination of the defect, or it is a **emergency or emergency measure**.\n- In most cases, you should not repair the defect yourself until you have asked your landlady to correct it and the defect has not been rectified afterwards.","- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annullierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1.500km)**, **400€ (zwischen 1.500km und 3.500km)** oder **600€ (über 3.500km)**.":"- A claim to **Compensation** is the right to a **Compensation** from your airline.\n- You will receive a chargeback if you were unable to reach your destination or were delayed due to **delay**, **cancellation**, or **overbooking**.\n- The **amount of payment** depends on the **flight route** and is **€250 (under 1,500 km)**, **€400 (between 1,500 km and 3,500 km)** or **€600 (over 3,500 km)**.","- Ein Anspruch auf **Betreuungsleistungen** ist das Recht auf zusätzliche Unterstützung wie Essen und Getränke, wenn sich Ihr **Abflug stark verzögert**.\n- Betreuungsleistungen müssen **angemessen zur Wartezeit und Tageszeit** sein. (Kleine) Mahlzeiten und Getränke sowie Zugang zu  Telefon oder Internet sind immer angemessen.\n- Wenn **lange Wartezeiten am Flughafen bei Nacht** entstehen, kann auch das Recht auf eine **Hotelunterbringung** mit Transport dorthin bestehen. \n- In der Regel werden Ihnen diese Leistungen **von der Fluglinie angeboten**; wenn nicht, können Sie **Schadensersatz** für notwendige Ausgaben mit Rechnungen beantragen.":"- A claim to **care services** is the right to additional support such as food and drinks if your **departure is severely delayed**.\n- Care services must be **appropriate to the waiting time and time of day**. (Small) meals and drinks, and access to telephone or internet are always appropriate.\n- If there are **long waiting times at the airport at night**, you may also have the right to a **hotel accommodation** with transport there. \n- As a rule, these services are offered to you **by the airline**; if not, you can claim **compensation** for necessary expenses with invoices.","- Ein Anspruch auf **Mietminderung** ist das Recht, eine **geringere Gesamtmiete zu zahlen**, wenn die **Nutzung der Wohnung**\ndurch einen Mangel **erheblich eingeschränkt** ist. \n- Ein **erheblicher Mangel** liegt vor, wenn der Mangel nicht nur optisch und die Wohnqualität stark beeinträchtigt ist.\n- Der Zeitraum der Minderung **beginnt mit der Anzeige des Mangels** bei der Vermieter:in, und **endet mit der Beseitigung**.\n- Sie sollten nicht sofort eine geringere Miete zahlen, da Sie so eine Kündigung wegen Mietverzug riskieren können. Stattdessen können Sie Ihrer Vermieterin melden, die **Miete unter Vorbehalt zu zahlen**. Nach Beseitigung des Mangels können Sie dann einen Anteil zurückfordern.\n- Um **wie viel Prozent** die Gesamtmiete gekürzt werden kann, hängt von der **Schwere des Mangels** ab. Einen Anhaltspunkt für Ihren Fall können Sie in einer **Mietminderungstabelle** im Internet finden.":"- A claim to**rent reduction** is the right to pay a **lower total rent** if the**use of the apartment**\nis **significantly limited** due to a defect. \n- There is a **significant deficiency** if the defect is not only visually and the quality of living is severely impaired.\n- The reduction period **begins with the notification of the defect** to the landlord:in, and **ends with remediation**.\n- You shouldn't pay a lower rent right away, as you can risk termination due to delay in rent. Instead, you can report to your landlady that**pay the tentative rent**. Once the defect has been rectified, you can claim back a share.\n- **how many percent** the total rent can be reduced, depends on the **severity of the defect**. You can find a clue for your case in a **rent reduction table** on the Internet.","- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mietende können für Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag festgelegt wurde.":"- A claim to **remedial defects** is the right to ask your **landlord to remedy the defect**.\n- Landlords have the duty to hand over and maintain the rented apartment in a usable condition.\n- If a defect occurs during the rental period, the landlord must eliminate it.\n- Exception: End of rental can be responsible for small repairs up to around 100 euros if this has been stipulated in the rental agreement.","- Ein Anspruch auf **Rückerstattung der Flugscheinkosten** ist das Recht, Ihren gezahlten Ticketpreis bei Annullierung innerhalb einer Woche zurückzuerhalten.\n- Zusätzlich erhalten Sie einen Rückflug zu Ihrem ersten Abflugort, wenn ein Anschlussflug annuliert wurde.\n- Statt der Rückerstattung können Sie auch eine anderweitige Beförderung zu Ihrem Zielort durch die Fluglinie wählen.":"- A claim to **Ticket Fee Refund** is the right to receive your paid ticket price back within one week if cancelled.\n- In addition, you will receive a return flight to your first departure location if a connecting flight has been cancelled.\n- You can choose to transfer the airline to your destination instead of reimbursement.","- Ein Anspruch auf **Schadensersatz** ist das Recht, sich **Geld** für durch den Mangel **entstandene Schäden erstatten** zu lassen.\n- **Mögliche Schäden** sind entstandene **Hotelkosten**, Wertverlust bei unbrauchbar gewordenen **Möbeln**, oder auch Schmerzensgeld bei körperlichen Schäden.\n- Wichtig ist, dass die Vermieter:in den Mangel zu vertreten hat, mit der Beseitigung in Verzug ist, oder diesen bei Vertragsabschluss verschwiegen hat.":"- A claim to **damages** is the right to be reimbursed to **money** for damages incurred as a result of the defect.\n- **Possible damage** includes incurred **hotel costs**, loss of value for **furniture** that has become unusable, or compensation for physical damage.\n- It is important that the lessor is responsible for the defect, is in arrears with the rectification, or has not disclosed it when concluding the contract.","Aufwendungsersatz":"Reimbursement of expenses","Ausgleichszahlung":"Chargeback","Außergerichtliche Einigung":"Out-of-court settlement","Beratungsangebote":"Legal advice","Beseitigung des Mangels durch die Vermieter:in":"Removal of the defect by the landlords","Betreuungsleistungen":"Care services","Die Justiz im Netz.":"Justice online.","Diese Option ist leider noch nicht verfügbar.":"Unfortunately, this option is not yet available.","Dokumentation des Mangels":"Documentation of the defect","Einfach, online, jederzeit.":"Simple, anywhere, at any time.","Erstberatung":"Initial consultation","Fall Sie sich unsicher fühlen, können Sie sich auch beraten lassen. Dafür haben wir <0>hier Beratungsangebote zusammengefasst.</0>":"If you feel unsure, you can also seek advice. For this purpose, we have <0>summarized consulting services here.</0>","Fluglinie kontaktieren":"Contact airline","Gerichtsverfahren":"Trials","Handlungsoptionen vergleichen":"Compare options for action","In welchem Bereich Ihres Lebens haben Sie ein Problem?":"What area of your life do you have a problem in?","Info":"Info","Ja":"Yes","Jetzt starten!":"Start now!","Lösungsfinder":"Solutionfinder","Mehr erfahren":"Learn more","Mietminderung":"Rent reduction","Mängelanzeige bei Ihrer Vermieter:in":"Notice letter of defects to your landlord","Mögliche Ansprüche":"Possible claims","Mögliche Lösungen":"Possible solutions","Nein":"No","Nur kostenlose Angebote anzeigen":"Show free offers only","Nächste Schritte":"Next steps","Online Klage einreichen":"File a lawsuit online","Optionen":"Options","Problem einordnen":"Classify issue","Rechtlich beraten lassen":"Get legal advice","Rechtsproblem einordnen":"Classify legal problem","Rückerstattung der Flugscheinkosten":"Ticket reimbursement","Schadensersatz":"Damages","Schlichtungsantrag stellen":"Submit a request for conciliation","Schritt":"Step","Startseite":"Home page","Unser Angebot":"Our offer","Vermieter:in erneut kontaktieren":"Contact Landlord in again","Wegweiser":"Wegweiser","Weitere Möglichkeiten":"Weitere Möglichkeiten","Wie es funktioniert":"How it works","Wobei können wir Ihnen helfen?":"What can we help you with?","ZPO Informationen":"ZPO information","Zum Prototypen":"Go to the prototype","Zurück":"Back","Zurück zur Homepage":"Back to homepage","back":"Back","consultation.header":"Legal advice","consultation.sub_header":"What do you need support with?","exit.footer":"Are you looking for more support? Then you can contact legal advice centres who can inform you individually about your case. In a consultation, experts assess whether legal claims exist and how you can best enforce them. For this purpose, we have [provided an overview of advice centres here](/advisory services).","exit.heading":"Unfortunately, we didn't cover your case.","forward":"Continue","home.hero_offer":"Justizportal.de is an offer of the Federal Ministry of Justice and Consumer Protection in cooperation with the federal ministries of justice in the federal states. The portal gives you an overview of your rights in various everyday situations. It also helps you to classify and solve your legal questions and problems. You will be informed about your options for action and will be supported in asserting your rights.","home.howitwork_claims":"We will then help you to evaluate which claims might apply in your case. Claims are rights to demand a certain action from others (e.g. payment of damages), to tolerate (e.g. subletting a flat) or to refrain from (e.g. entering a property).","home.howitwork_classify":"First, we'll help you clarify whether there is a legal problem in your case. We will show you information tailored to your case.","home.howitwork_heading":"How it works","home.howitwork_lawsuit":"When all other options are exhausted, we'll help you file a claim with the district court online. To this end, we explain the process of our online process to you and provide transparent information on the costs of legal proceedings as well as possible legal advice.","home.howitwork_options":"Based on this, we will suggest possible solutions for <0>you - initially for an amicable dispute resolution with the opposite side.</0>","home.howitwork_subheading":"We can help you solve your legal problem in four easy steps:","home.howtohelp_footer":"You can find out more about the legal problems we can help you with on the following pages.","home.sub_heading":"We inform you about your rights in everyday life and help you solve your legal problem in simple steps","nextsteps.header":"Solution Finder End: Possible Next Steps","nextsteps.sub_header":"We've summarized next steps to enforce your claims here.<0/> Click the boxes one at a time to learn more.","pe.header":"Possible legal claims","pe.sub_header":"The following claims could result from your information. A claim is the right to demand something from another person or company. You can also enforce multiple claims at the same time. \nClick on the claims to learn more about them. We will then inform you about possible next steps on the next page.","pe.to_next_steps":"To the next steps","zpo.nextsteps":"1. On the next page, you can create the document for a lawsuit related to your case. \n2. We will estimate the legal costs for you and inform you about the next steps you need to take. \n3. Once created, you can review all information first. **Only then do you decide on the submission.**","Über Ansprüche informieren":"Inform about claims"}};